.appBarHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-image: linear-gradient(#00C2FF, #0040FF);
}

.profileLogo {
    width: 17%;
    padding-left: 2%;
    padding-top: 1%;
    padding-bottom: 1%;
}

.contactContainer {
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    /* margin: 1%; */
    width: 20%;
}

.contactIcon {
    height: 1.5rem;
    width: 1.5rem;
    color: #fff !important;
}

.phoneBtn,
.linkedinBtn,
.githubBtn,
.emailBtn {
    display: flex;
    flex-direction: row;
    margin: 1% !important;
    width: 13rem;
    /* border: 1px solid #1f1f1f;
    border-radius: 7px; */
}

.contactInfo {
    padding-left: 7%;
    text-transform: none !important;
    font-size: 1rem;
    font-weight: 500;
    color: #1f1f1f;
}